"use client";
import Image from 'next/image';
import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';

// FontAwesome
import { config } from '@fortawesome/fontawesome-svg-core';
import '@fortawesome/fontawesome-svg-core/styles.css';
config.autoAddCss = false;
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHouse, faPlus, faBars, faSun } from '@fortawesome/free-solid-svg-icons';

// Images
import iscore from '../images/iscore.png';



// Message type
type Message = {
  sender: 'user' | 'bot' | 'system';
  text: string;
  timestamp: string;
};

// Utility: Format time
const formatTime = (date: Date): string => {
  return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
};

// Bot reply logic
const botReply = (userMessage: string): string => {
  return `You said: "${userMessage}"`;
};

type ChatBoxProps = {
  resetTrigger: number;
};

// ChatBox Component
function ChatBox({ resetTrigger }: ChatBoxProps) {

  useEffect(() => {
    resetChat();
  }, [resetTrigger]); // ← already runs on mount and when resetCounter changes


  const [messages, setMessages] = useState<Message[]>([
    { sender: 'bot', text: 'Hi! How can I help you today?', timestamp: formatTime(new Date()) }
  ]);
  const [input, setInput] = useState<string>('');
  const [isTyping, setIsTyping] = useState<boolean>(false);


  
  const sendMessage = (): void => {
    if (input.trim() === '') return;

    const userMsg: Message = {
      sender: 'user',
      text: input,
      timestamp: formatTime(new Date())
    };

    setMessages((prev) => [...prev, userMsg]);
    setInput('');
    setIsTyping(true);

    setTimeout(() => {
      const reply: Message = {
        sender: 'bot',
        text: botReply(userMsg.text),
        timestamp: formatTime(new Date())
      };
      setMessages((prev) => [...prev, reply]);
      setIsTyping(false);
    }, 1500);
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>): void => {
    if (e.key === 'Enter') sendMessage();
  };

  const resetChat = (): void => {

    const now = new Date();
    const formattedDate = now.toLocaleDateString(undefined, {
      weekday: 'short',
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  
    setMessages([
    {
      sender: 'system',
      text: `${formattedDate}`,
      timestamp: formatTime(now)
    },
    {
      sender: 'bot',
      text: 'Hi! How can I help you today?',
      timestamp: formatTime(new Date())
    }
  ]);
};

  return (
    <div className="flex flex-col h-full justify-between p-4">
      {/* Chat messages */}
      <div className="flex-1 overflow-y-auto space-y-4 pr-2">
      {messages.map((msg, index) => (
        msg.sender === 'system' ? (
          <div key={index} className="flex justify-center my-4">
            <div className="text-sm text-gray-500 italic">
              ─── {msg.text} ───
            </div>
          </div>
        ) : (
          <div key={index} className={`flex flex-col ${msg.sender === 'user' ? 'items-end' : 'items-start'}`}>
            <div
              className={`max-w-[70%] px-4 py-2 rounded-2xl shadow 
              ${msg.sender === 'user' ? 'bg-blue-500 text-white' : 'bg-gray-200 text-gray-800'}`}>
              <p>{msg.text}</p>
            </div>
            <p className="text-xs text-gray-500 mt-1 px-2">
              {msg.timestamp}
            </p>
          </div>
        )
      ))}


        {isTyping && (
          <div className="flex justify-start">
            <div className="bg-gray-100 text-gray-600 px-4 py-2 rounded-2xl max-w-[70%] italic">
              Bot is typing...
            </div>
          </div>
        )}
      </div>

      {/* Input */}
      <div className="mt-4 flex items-center border-t pt-3">
        <input
          type="text"
          placeholder="Type your message..."
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={handleKeyDown}
          className="flex-1 border border-gray-300 rounded-full px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400"
        />
        <button onClick={sendMessage} className="ml-3 bg-[#3ec1c7] text-white px-4 py-2 rounded-full hover:bg-[#4f3795] transition">
          Send
        </button>
      </div>
    </div>
  );
}



// Main Page
export default function Home() {

  const router = useRouter(); //to switch between pages

  const [resetCounter, setResetCounter] = useState(0);
  const handleReset = () => {
    setResetCounter(prev => prev + 1);
  };


  return (
    <div>
      <div className="main h-screen flex flex-col items-center">
        <div className="navBar h-[10%] w-[95%] flex flex-row items-center justify-center space-x-18 py-11">
          <div className='left flex gap-10 w-[30%] justify-center'>
            <button type='button' className='icons text-2xl'><FontAwesomeIcon icon={faHouse} onClick={() => router.push('/homepage')}/></button>
            <button type='button' className='icons text-2xl'><FontAwesomeIcon icon={faPlus} onClick={handleReset}/></button>
          </div>
          <div className='middle w-[30%] flex items-center justify-center gap-5'>
            <p className='font-bold text-white text-3xl'>Chat With</p>
            <Image src={iscore} alt="iScore" width={150} />
          </div>
          <div className='right w-[30%] flex justify-center gap-10'>
            <button type='button' className='icons text-2xl'><FontAwesomeIcon spin icon={faSun} /></button>
            <button type='button' className='icons text-2xl'><FontAwesomeIcon icon={faBars} /></button>
          </div>
        </div>

        {/* Chat Section */}
        <div className="chat bg-white h-[83%] w-[95%] rounded-4xl">
          <ChatBox resetTrigger={resetCounter} />
        </div>

      </div>
    </div>
  );
}